import './index.css';
import Image from '../images/Image.png';
import { useEffect } from 'react';
import Page from '../components/Page';
const CreateNFT = () => {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <Page title="FANLEB" sx={{}}>
      <div className='create section__padding'>
        <div className="create-container">
          <h1>NFT </h1>
          <p className='upload-file'> </p>
          <div className="upload-img-show">
              <h3>JPG, PNG, GIF, SVG, WEBM, MP3, MP4. Max 100mb.</h3>
              <img src={Image} alt="banner" />
              <p>   .</p>
          </div>
          <form className='writeForm' autoComplete='off'>
            <div className="formGroup">
              <label></label>
              <input type="file" className='custom-file-input'
            />
            </div>
            <div className="formGroup">
              <label></label>
              <input type="text" placeholder=' ' autoFocus={true} />
            </div>
            <div className="formGroup">
              <label></label>
              <textarea type="text" rows={4}
            placeholder='   ' 
            ></textarea>
            </div>
            <div className="formGroup">
              <label></label>
              <div className="twoForm">
                <input type="text" placeholder=''  />
                <select>
                  <option value="ETH">ETH</option>
                  <option value="BTC">BTC</option>
                  <option value="LTC">LTC</option>
                </select>
              </div>
            </div>
            <div className="formGroup">
              <label></label>
              <select >
                <option>Art</option>
                <option>Photography</option>
                <option>Sports</option>
                <option>Collectibles</option>
                <option>Trading Cards</option>
                <option>Utility</option>
              </select>
            </div>
            {/* <div className="formGroup">
              <label>Available Items</label>
              <input type="text" placeholder='No of Items'/>
            </div> */}
            <button className='writeButton'> </button>
          </form>
        </div>
      </div>
    </Page>
  )
};

export default CreateNFT;